import React, { useState, useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import { Howl } from 'howler';
import Confetti from 'react-confetti';
import mp3 from "./audio/ramadan.wav";
import './index.css'; 
import Footer from './Footer';

const App = () => {
  const [isClicked, setIsClicked] = useState(false);
  const soundRef = useRef(null);

  const animation = useSpring({
    opacity: isClicked ? 1 : 0,
    transform: isClicked ? 'scale(1)' : 'scale(0)',
  });

  const handleButtonClick = () => {
    setIsClicked(!isClicked);

    if (!soundRef.current) {
      soundRef.current = new Howl({
        src: [mp3],
      });
    }

    if (!isClicked) {
      soundRef.current.play();
    } else {
      soundRef.current.stop();
    }
  };

  return (
    <div className='app'>
      <div className="app-container">
        <h1 className='title'>Salam 🎉</h1>
        <button onClick={handleButtonClick} className='btn'>Click me for a surprise!</button>

        <animated.div style={animation} className="birthday-decoration">
          🌙🕋🙏
          <div className='words-container'>
            <h1 className='div-title'>❤️ رمضان كريم </h1>
            <p className='words'>أتقدم بأطيب التهاني بحلول شهر رمضان المعظم، وأتمنى لكم ولأحبتكم شهرًا مليئًا بالبركات والفرح والسعادة ، اللهم اجعل رمضان هذا شفاء لكل مريض، وفرجًا من كل كرب وضيق، واستجابة لدعائنا ونصرًا ورفعة للمسلمين، واسألك يا الله أن تتغمد برحمتك المفقودين من أحبتنا وأمواتنا، وتغفر لهم وترحمهم، وتسكنهم فسيح جناتك مع الصديقين والشهداء والصالحين ، فإنك على كل شيء قدير. كل عام وكل الأمة الإسلامية بخير وصحة وسعادة، كل عام وانت ❤️ بخير. </p>
          </div>
        </animated.div>

        {isClicked && <Confetti />}
      </div>
      <Footer />
    </div>
  );
};

export default App;
